import React, { JSX, useEffect, useMemo, useRef, useState } from 'react';
import css from './Messages.module.scss';
import cn from 'classnames';
import { useParams } from 'react-router-dom';

import { MessageForm } from 'features/MessageForm/MessageForm';

import { IProfile } from 'entities/profile/types/types';
import { IMessage } from 'entities/messages/types/types';

import { selectMe } from 'entities/auth';
import {
    Members,
    Message,
    creatWSAction,
    getMessagesAction,
    loadPrevMessagesAction,
    selectIsFetching,
    selectMessages,
    setMessages
} from 'entities/messages';
import { getDialogsActions, selectDialogs } from 'entities/dialogs';
import { getProfileAction } from 'entities/profile';
import { idOfStringToIdNumberOfArray, scrollToBottom } from 'shared/helpers/helpers';
import { SpinerOverlay } from 'shared/UI/Spiner/Spiner';
import { useAppDispatch, useAppSelector } from 'shared/store/hooks';

import { ReactComponent as SvgIconUsers } from 'shared/assets/icons/users.svg';
import { ReactComponent as SvgIconClose } from 'shared/assets/icons/close.svg';

export function Messages(): JSX.Element {
    const params = useParams<{ id: string }>();
    const messages = useAppSelector(selectMessages);
    const isFetching = useAppSelector(selectIsFetching);
    const dispatch = useAppDispatch();
    const me = useAppSelector(selectMe);
    const dialogs = useAppSelector(selectDialogs);
    const [profiles, setProfile] = useState<IProfile[]>([]);
    const listContainer = useRef<HTMLDivElement>(null);
    const topEl = useRef<HTMLDivElement>(null);
    const [isScollTop, setIsScrollTop] = useState<boolean>(false);
    const [isMembersVisible, setMembersVisible] = useState<boolean>(false);

    useEffect((): () => void => {
        dispatch(getMessagesAction(Number(params.id)));
        dispatch(getDialogsActions());
        const ws = new Promise<WebSocket>(res => {
            dispatch(creatWSAction(res));
        })
        return () => {
            ws.then(ws => ws.close(1000, 'the work is finished'));
            dispatch(setMessages([]));
        };
    }, [])

    useEffect(() => {
        const callback = (entries: IntersectionObserverEntry[]) => {
            if (entries[0].isIntersecting) {
                setIsScrollTop(true);
                dispatch(loadPrevMessagesAction(Number(params.id)));
            }
        }
        const options: IntersectionObserverInit = {
            root: listContainer.current,
            rootMargin: "25% 0px 0px 0px",
        }
        const observer = new IntersectionObserver(callback, options);

        if (dialogs.length) {
            const dialog = dialogs.find(dialog => dialog.id === Number(params.id));
            if (dialog) {
                const members = idOfStringToIdNumberOfArray(dialog.members);
                members.forEach(id => {
                    dispatch(getProfileAction(id, (profile: IProfile) => {
                        setProfile(items => [...items, profile]);
                    }))
                })
            }

            if (listContainer.current) {
                scrollToBottom(listContainer.current);
            }

            if (topEl.current) {
                observer.observe(topEl.current);
            }
        }

        return () => {
            observer.disconnect()
            setProfile([]);
        };
    }, [dialogs]);

    useEffect(() => {
        if (!isScollTop && listContainer.current) {
            scrollToBottom(listContainer.current);
        } else {
            setIsScrollTop(false);
        }
    }, [messages])

    const filteredMessages = useMemo((): IMessage[] => {
        const idOfMessages = messages.map(mess => mess.id);
        const uniqueIdOfMessages = Array.from(new Set(idOfMessages));
        const filteredMessages = uniqueIdOfMessages.map(id => {
            return messages.find(mess => mess.id === id);
        });
        return filteredMessages as IMessage[];
    }, [messages])

    if (params.id) {
        return (
            <>
                {isFetching && <SpinerOverlay size={1.5} />}
                <div className={css.messages}>
                    <button
                        onClick={() => setMembersVisible(boolValue => !boolValue)}
                        className={css.messages__members_toggle_btn}
                    >
                        {isMembersVisible
                            ? <SvgIconClose />
                            : <SvgIconUsers />
                        }
                    </button>
                    <Members
                        isShow={isMembersVisible}
                        profiles={profiles}
                        dialogId={Number(params.id)}
                        setProfiles={setProfile}
                    />

                    <div ref={listContainer} className={css.messages__list}>
                        <div ref={topEl} style={{ minHeight: '1px' }}></div>
                        {filteredMessages.map(message => {
                            const profile = profiles.find(profile => profile.userId === message.userId);
                            return (
                                <Message
                                    key={message.id}
                                    message={message}
                                    profile={profile}
                                    variant={message.userId === me?.id ? 'owner' : 'companion'}
                                    dialogId={Number(params.id)}
                                />
                            )
                        }
                        )}
                    </div>
                    <div className={css.messages__footer}>
                        <MessageForm dialogId={Number(params.id)} />
                    </div>
                </div>
            </>
        );
    } else {
        return <SpinerOverlay size={1.5} />
    }
}


