import React, { JSX } from 'react';
import css from './LanguageSwitcher.module.scss';
import cn from 'classnames';
import { useAppDispatch, useAppSelector } from 'shared/store/hooks';
import { selectLanguage, toggleLanguageAction } from 'features/Language/store/slice';

export function LanguageSwitcher(): JSX.Element {
    const currentLanguage = useAppSelector(selectLanguage);
    const dispatch = useAppDispatch();

    const toggleLanguage = () => {
        dispatch(toggleLanguageAction());
    }
    return (
        <div onClick={toggleLanguage} className={css.switcher}>
            <div className={cn(css.switcher__lang, { [css.active]: currentLanguage === 'en' })}>En</div>
            /
            <div className={cn(css.switcher__lang, { [css.active]: currentLanguage === 'ua' })}>Ua</div>
        </div>
    );
}