import React, { JSX, useRef } from 'react';
import css from './MessageForm.module.scss';
import cn from 'classnames';

import { useAppDispatch } from 'shared/store/hooks';
import { createMessageAction } from 'entities/messages';

import { ReactComponent as SvgIconSend } from 'shared/assets/icons/send.svg';


interface MessageFormProps {
    dialogId: number;
}

export function MessageForm({ dialogId }: MessageFormProps): JSX.Element {
    const dispatch = useAppDispatch();
    const inputRef = useRef<HTMLDivElement>(null)
    const sendMessage = () => {
        if (inputRef.current) {
            if (inputRef.current.innerText.trim().length === 0) return;
            dispatch(createMessageAction(dialogId, inputRef.current.innerText));
            inputRef.current.innerText = '';
        }
    }
    return (
        <div className={css.message_form}>
            <div className={css.message_form__input_wrap}>
                <div ref={inputRef} contentEditable className={css.message_form__input}></div>
            </div>
            <button onClick={sendMessage} className={css.message_form__btn}>
                <SvgIconSend />
            </button>
        </div>
    );
}