import { IRootApi, RootApi } from "shared/api/RootApi";
import { IMessage } from "../types/types";

interface IGetMessagesResponse extends IRootApi {
    data: {
        items: IMessage[];
        totalCount: number;
    }
}

interface ICreateMessageResponse extends IRootApi {
    data: IMessage;
}
interface IDeleteMessageResponse extends IRootApi {
    data: {
        message: string;
    };
}

interface IParams {
    count: number;
    page?: number;
}

export abstract class MessagesApi extends RootApi {
    static async getMessages(dialogId: number, count: number, page?: number) {
        return this.catchAxiosError<IGetMessagesResponse>(async () => {
            let params: IParams = {
                count
            }

            if(page) {
                params.page = page;
            }

            let response = await this.axios.get('/dialogs/messages/' + dialogId, {
                params
            });
            return response;
        })
    }

    static async createMessage(dialogId: number, text: string) {
        return this.catchAxiosError<ICreateMessageResponse>(async () => {
            let response = await this.axios.post('/dialogs/messages', {
                dialogId,
                text
            });
            return response;
        })
    }

    static async deleteMessage(dialogId: number, messageId: number) {
        return this.catchAxiosError<IDeleteMessageResponse>(async () => {
            let response = await this.axios.delete('/dialogs/messages', {
                data: {
                    dialogId,
                    messageId
                }
            });
            return response;
        })
    }

    static createWs(): WebSocket {
        return new WebSocket(`ws://localhost:8000/ws?token=${this.jwt}`);
    }
}