import { IRootApi, RootApi } from "shared/api/RootApi";
import { IDialog } from "../types/types";

interface IDialogResponse extends IRootApi {
    data: {
        id: number;
        members: string;
    }
}
export interface IGetDialogsResponse extends IRootApi {
    data: {
        items: IDialog[]
    }
}
export interface ICreateDialogResponse extends IDialogResponse {}
export interface IDeleteDialogResponse extends IRootApi {
    data: {
        message: string;
    }
}
export interface IAddUserToDialogResponse extends IDialogResponse {}
export interface IRemoveUserFromDialogResponse extends IDialogResponse {}


export abstract class DialogsApi extends RootApi {
    static async getDialogs() {
        return this.catchAxiosError<IGetDialogsResponse>(async () => {
            let response = await this.axios.get('/dialogs');
            return response;
        })
    }

    static async createDialog(userId: number) {
        return this.catchAxiosError<ICreateDialogResponse>(async () => {
            let response = await this.axios.post('/dialogs', { userId });
            return response;
        })
    }

    static async deleteDialog(dialogId: number) {
        return this.catchAxiosError<IDeleteDialogResponse>(async () => {
            let response = await this.axios.delete('/dialogs', { data: {
                dialogId
            } });
            return response;
        })
    }


    static async addUserToDialog(dialogId: number, userId: number) {
        return this.catchAxiosError<IAddUserToDialogResponse>(async () => {
            let response = await this.axios.post('/dialogs/addUser', {
                dialogId,
                userId
            });
            return response;
        })
    }

    static async removeUserFromDialog(dialogId: number, userId: number) {
        return this.catchAxiosError<IRemoveUserFromDialogResponse>(async () => {
            let response = await this.axios.post('/dialogs/removeUser', {
                dialogId,
                userId
            });
            return response;
        })
    }
}