import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { LanguageState } from '../types/types';
import { AppDispatch, RootState } from 'shared/store/types';
import { store } from 'app/store/store';

interface LanguageSliceState {
    lang: LanguageState;
}

const initialState: LanguageSliceState = {
    lang: 'en',
}

export const languageSlice = createSlice({
    name: 'dialogs',
    initialState,
    reducers: {
        setLanguage(state, action: PayloadAction<LanguageState>) {
            state.lang = action.payload;
        }
    }
});


export const selectLanguage = (state: RootState): LanguageState => state.languageReducer.lang;

export const checkInstalledLanguageAction = () => async (dispatch: AppDispatch) => {
    if (localStorage.getItem('lang') === null) {
        localStorage.setItem('lang', 'en')
    } else {
        const languageState = localStorage.getItem('lang') as LanguageState;
        dispatch(setLanguage(languageState));
    }
}

export const toggleLanguageAction = () => async (dispatch: AppDispatch) => {
    const state = store.getState();
    const { lang } = state.languageReducer;
    lang === 'en' ? dispatch(setLanguage('ua')) : dispatch(setLanguage('en'));
}
export const {
    setLanguage
} = languageSlice.actions;

export const languageSliceReducer = languageSlice.reducer;