import React, { JSX, useEffect, useState } from 'react';
import css from './Dialogs.module.scss';
import cn from 'classnames';
import { List } from 'shared/UI/List/List';
import { useAppDispatch, useAppSelector } from 'shared/store/hooks';
import {
    DialogCard,
    getDialogsActions,
    selectDialogs,
    selectIsFetching,
} from 'entities/dialogs';
import { Link } from 'react-router-dom';
import { IDialog } from 'entities/dialogs/types/types';
import { SpinerOverlay } from 'shared/UI/Spiner/Spiner';
import { selectMe } from 'entities/auth';
import { getProfileAction } from 'entities/profile';
import { IProfile } from 'entities/profile/types/types';
import { DialogOptions } from 'features/DialogOptions/DialogOptions';
import { idOfStringToIdNumberOfArray } from 'shared/helpers/helpers';
import { getText, selectLanguage } from 'features/Language';

interface IDialogsData {
    dialog: IDialog;
    profile: IProfile;
}

export function Dialogs(): JSX.Element {
    const dialogs = useAppSelector(selectDialogs);
    const isFetching = useAppSelector(selectIsFetching);
    const dispatch = useAppDispatch();
    const me = useAppSelector(selectMe);
    const [isDialogsLoaded, setIsDialogsLoaded] = useState<boolean>(false);
    const [dialogsData, setDialogsData] = useState<IDialogsData[]>([]);
    const currentLanguage = useAppSelector(selectLanguage);

    const loadDialogDataByChunks = async (dialogs: IDialog[], viewerId: number,) => {
        const loadProfile = async (profileId: number): Promise<IProfile> => {
            return new Promise(resolve => {
                dispatch(getProfileAction(profileId, resolve))
            })
        }

        for await (const dialogData of loadProfileByDialog(dialogs, viewerId, loadProfile)) {
            setDialogsData(data => [...data, dialogData]);
        }
    }

    useEffect(() => {
        dispatch(getDialogsActions(() => setIsDialogsLoaded(true)));
    }, []);

    useEffect(() => {
        if (me && isDialogsLoaded) {
            loadDialogDataByChunks(dialogs, me.id);
        }
    }, [isDialogsLoaded])

    useEffect(() => {
        if (isDialogsLoaded && dialogs.length < dialogsData.length) {
            const fileterdDialogsData = dialogsData.filter(dialogData => {
                if (dialogs.find(dialog => dialog.id === dialogData.dialog.id)) {
                    return dialogData;
                }
            });
            setDialogsData(fileterdDialogsData);
        }
    }, [dialogs])

    return (
        <>
            {isFetching && <SpinerOverlay size={1.5} />}
            <div className={css.dialogs}>
                <h2 className='mb-3'>
                    {
                        getText(currentLanguage, {
                            en: 'Dialogs',
                            ua: 'Діалоги'
                        })
                    }
                </h2>
                <List
                    items={dialogsData}
                    renderItem={
                        ({ dialog, profile }, style) =>
                            <DialogCard
                                key={dialog.id}
                                dialog={dialog}
                                profile={profile}
                                dialogOptions={() => <DialogOptions dialogId={dialog.id} />}
                                style={style}
                            />
                    }
                    emptyState={
                        () =>
                            <h6 className='text-center'>
                                You don't have any messages yet. <br /> You can find new people <Link to='/users'>here</Link>
                            </h6>
                    }
                />
            </div>
        </>
    );
}

async function* loadProfileByDialog(
    dialogs: IDialog[],
    viewerId: number,
    fetchProfile: (profileId: number) => Promise<IProfile>
) {
    for (const dialog of dialogs) {
        const members = idOfStringToIdNumberOfArray(dialog.members);
        const id = members[1] === viewerId ? members[0] : members[1];
        const profile = await fetchProfile(id);
        yield { dialog, profile }
    }
}
