import React, { InputHTMLAttributes, JSX, Ref, useState } from 'react';
import css from './Input.module.scss';
import cn from 'classnames';
import { ReactComponent as SvgIconEye } from './eye.svg';
import { ReactComponent as SvgIconEyeOff } from './eye-off.svg';

interface InputProps extends InputHTMLAttributes<HTMLInputElement> {
    variant?: 'light' | 'dark';
    defaultValue?: string;
    errorState: boolean;
}


export const Input = React.forwardRef(({ variant = 'light', defaultValue, errorState = false, ...props }:InputProps , ref: Ref<HTMLInputElement>) => {
    const classname = cn(
        css.input,
        { [css.light]: variant === 'light' },
        { [css.dark]: variant === 'dark' },
        { [css.error]: errorState }
    );

    return <input
        defaultValue={defaultValue}
        className={classname}
        {...props}
        ref={ref}
    />
})

export const InputPassword = React.forwardRef(({ variant = 'light', defaultValue, errorState = false, ...props }:InputProps , ref: Ref<HTMLInputElement>) => {
    const [type, setType] = useState<'password' | 'text'>('password');
    
    return (
        <div className={css.input_password}>
            <Input type={type} variant={variant} defaultValue={defaultValue} errorState={errorState} ref={ref} {...props}/>
            <div className={css.input_password_icon} onClick={() => setType(type => type === 'password' ? 'text' : 'password')}>
                {type === 'password' ? <SvgIconEyeOff /> : <SvgIconEye />}
            </div>
        </div>
    );
})