import { AppDispatch } from "shared/store/types"
import { setDialogs, setIsFetching } from "./slice"
import { DialogsApi } from "../api/DialogsApi";
import { IDialog } from "../types/types";

export const getDialogsActions = (onLoaded?: (dialogs: IDialog[]) => void) => async (dispatch: AppDispatch) => {
    dispatch(setIsFetching(true));
    const response = await DialogsApi.getDialogs();
    if(response && response.data.resultCode === 0) {
        dispatch(setDialogs(response.data.data.items.reverse()));
        onLoaded && onLoaded(response.data.data.items);
    }
    dispatch(setIsFetching(false));
}

export const createDialogActions = (userId: number, callbackAfterFetch: (id: number) => void) => async (dispatch: AppDispatch) => {
    dispatch(setIsFetching(true));
    const response = await DialogsApi.createDialog(userId);
    if(response && response.data.resultCode === 0) {
        callbackAfterFetch(response.data.data.id);
    }
    dispatch(setIsFetching(false));
}

export const deleteDialogActions = (dialogId: number) => async (dispatch: AppDispatch) => {
    dispatch(setIsFetching(true));
    const response = await DialogsApi.deleteDialog(dialogId);
    if(response && response.data.resultCode === 0) {
        dispatch(getDialogsActions())
    }
    dispatch(setIsFetching(false));
}

export const addUserToDialogActions = (dialogId: number, userId: number, onFetched?: () => void) => async (dispatch: AppDispatch) => {
    const response = await DialogsApi.addUserToDialog(dialogId, userId);
    if(response && response.data.resultCode === 0) {
        onFetched && onFetched();
    }
}
export const removeUserFromDialogActions = (dialogId: number, userId: number, onFetched?: () => void) => async (dispatch: AppDispatch) => {
    const response = await DialogsApi.removeUserFromDialog(dialogId, userId);
    if(response && response.data.resultCode === 0) {
        onFetched && onFetched();
    }
}