import React, { ChangeEvent, JSX, useEffect, useMemo } from 'react';
import css from './Users.module.scss';
import cn from 'classnames';
import { Input } from 'shared/UI/Input/Input';
import { IUser } from 'entities/users/types/types';
import {
    UserCard,
    getUsersAction,
    selectFriend,
    selectIsFetching,
    selectTerm,
    selectUsers,
    setFriend,
    setIsFetching,
    setTerm
} from 'entities/users';
import { Button } from 'shared/UI/Button/Button';
import { useAppDispatch, useAppSelector } from 'shared/store/hooks';
import { SpinerOverlay } from 'shared/UI/Spiner/Spiner';
import { List } from 'shared/UI/List/List';
import { FollowUnfollw } from 'features/followUnfollw';
import { useLocation, useNavigate } from 'react-router-dom';
import { IMessagesLocationState } from 'entities/messages/types/types';
import { addUserToDialogActions } from 'entities/dialogs/store/actions';
import { SendMessage } from 'features/SendMessage/SendMessage';
import { getText, selectLanguage } from 'features/Language';


export function Users(): JSX.Element {
    const users = useAppSelector(selectUsers);
    const friend = useAppSelector(selectFriend);
    const term = useAppSelector(selectTerm);
    const isFetching = useAppSelector(selectIsFetching);
    const dispatch = useAppDispatch();
    const navigation = useNavigate();
    const location: { state: IMessagesLocationState | null } = useLocation();
    const { state } = location;
    const currentLanguage = useAppSelector(selectLanguage);

    useEffect(() => {
        dispatch(getUsersAction());
    }, [term, friend])

    const onAddUserToDialogHandlre = (dialogId: number, userId: number) => {
        dispatch(setIsFetching(true));
        dispatch(addUserToDialogActions(dialogId, userId, () => {
            dispatch(setIsFetching(false));
            navigation('/messages/' + dialogId);
        }));
    }

    const filteredUsers = state ?
        users.filter(user => {
            if (!state.currentMembers.find(profile => profile.userId === user.id)) {
                return user;
            }
        }) : users;

    return (
        <>
            {isFetching && <SpinerOverlay size={1.5} />}
            <div className={css.users}>

                <h2 className='mb-3'>
                    {
                        getText(currentLanguage, {
                            en: 'Users',
                            ua: 'Люди'
                        })
                    }
                </h2>

                <Input
                    type='text'
                    errorState={false}
                    placeholder={
                        getText(currentLanguage, {
                            en: 'Search...',
                            ua: 'Шукати...'
                        })
                    }
                    variant='dark'
                    value={term}
                    onChange={(e: ChangeEvent<HTMLInputElement>) => dispatch(setTerm(e.target.value))}
                />

                <div className={cn(css.filter, 'mt-3')}>
                    <button
                        className={cn(css.filter__btn, { [css.active]: !friend })}
                        onClick={() => dispatch(setFriend(false))}
                    >

                        {
                            getText(currentLanguage, {
                                en: 'All',
                                ua: 'Всі'
                            })
                        }
                    </button>
                    <button
                        className={cn(css.filter__btn, { [css.active]: friend })}
                        onClick={() => dispatch(setFriend(true))}
                    >
                        {
                            getText(currentLanguage, {
                                en: 'Friends',
                                ua: 'Друзі'
                            })
                        }
                    </button>
                </div>

                <div className="mt-3">
                    <List
                        items={filteredUsers}
                        renderItem={(user: IUser) =>
                            <UserCard key={user.id} user={user} bottomSlot={() =>
                                <>
                                    {state
                                        ? <Button
                                            onClick={() => onAddUserToDialogHandlre(state.dialogId, user.id)}
                                        >
                                            {
                                                getText(currentLanguage, {
                                                    en: 'Add to dialog',
                                                    ua: 'Додати до діалогу'
                                                })
                                            }
                                        </Button>
                                        : <div className='d-flex gap-3'>
                                            <SendMessage userId={user.id} />
                                            <FollowUnfollw id={user.id} isFollow={user.followed} callbackAfterFetch={() => dispatch(getUsersAction())} />
                                        </div>
                                    }
                                </>
                            } />}
                        emptyState={() => <h6 className='text-center'>
                            {
                                getText(currentLanguage, {
                                    en: 'Users not found',
                                    ua: 'Людей не знайдено'
                                })
                            }
                        </h6>}
                    />
                </div>
            </div>
        </>
    );
}



