import React, { JSX } from 'react';
import css from './Img.module.scss';
import cn from 'classnames';
import imgPlug from './plug.jpg';

interface AvatarProps {
    src?: string | null;
}

export function Avatar({ src, ...props }: AvatarProps): JSX.Element {
    return (<>
        {src
            ? <img src={src} alt="" {...props} />
            : <img src={imgPlug} alt="" {...props} />
        }
    </>
    );
}