import React, { HtmlHTMLAttributes, JSX, ReactNode } from 'react';
import css from './DialogCard.module.scss';
import cn from 'classnames';
import { IDialog } from 'entities/dialogs/types/types';
import { IProfile } from 'entities/profile/types/types';
import { Link } from 'react-router-dom';
import { Box } from 'shared/UI/Box/Box';
import { CircleAvatar } from 'entities/users';

interface DialogCardProps extends HtmlHTMLAttributes<HTMLAnchorElement> {
    dialog: IDialog;
    profile: IProfile;
    dialogOptions: () => ReactNode;
}

export function DialogCard({ dialog, profile, dialogOptions, ...props }: DialogCardProps): JSX.Element {
    return (
        <Link className={css.card} to={'/messages/' + dialog.id} {...props}>
            <Box className={css.card__body}>
                <div className={css.card__photo}>
                    <CircleAvatar src={profile.photos.small} />
                </div>
                <div className={css.card__text}>
                    <div className={css.card__name}>{profile.fullName}</div>
                    {!!dialog.messages.length &&
                        <div className={css.card__last_mess}>
                            {dialog.messages[0].text.slice(0, 20) + '...'}
                        </div>
                    }
                </div>
                <div className={css.card__options}>
                    {dialogOptions()}
                </div>
            </Box>
        </Link>
    );
}