import { RootState } from "shared/store/types";
import { IMessage } from "../types/types";

export const selectIsFetching = (state: RootState): boolean => state?.messagesReducer?.isFetching || false;
export const selectMessages = (state: RootState): IMessage[] => state?.messagesReducer?.messages || [];
export const selectTotalCount = (state: RootState): number => state?.messagesReducer?.totalCount || 0;
export const selectCount = (state: RootState): number => state?.messagesReducer?.count || 0;
export const selectPage = (state: RootState): number => state?.messagesReducer?.page || 0;
