import axios, { AxiosError, AxiosInstance, AxiosResponse } from "axios";

export type CatchAxiosError = AxiosResponse | void;

export interface IRootApi {
    message: string;
    resultCode: 0 | 1;
}

export interface AxiosErrorWithData<T> extends AxiosError {
    data: T
}

export abstract class RootApi {
    static baseURL: string = 'http://localhost:8000';
    static axios: AxiosInstance = axios.create({
        baseURL: this.baseURL,
    })
    static jwt: string;

    static setPublick(): void {
        this.jwt = '';
        this.axios = axios.create({
            baseURL: this.baseURL,
        })
    }

    static setPrivate(jwt: string): void {
        this.jwt = jwt;
        this.axios = axios.create({
            baseURL: this.baseURL,
            headers: {
                'authorization': 'Bearer ' + jwt
            }
        })
    }

    static async catchAxiosError<T>(request: () => Promise<AxiosResponse>): Promise<AxiosResponse<T> | AxiosErrorWithData<T> | undefined > {
        try {
            return await request();
        } catch (error) {
            if(error instanceof AxiosError ) {
                return error.response;
            } 
                
            throw error;
            
        }
    }
}
