import React, { HtmlHTMLAttributes, JSX, ReactElement, ReactNode, useEffect, useRef, useState } from 'react';
import css from './DropDownOptions.module.scss';
import cn from 'classnames';
import { ReactComponent as SvgIconDots } from 'shared/assets/icons/dots-vertical.svg';

interface DropDownOptionsProps extends HtmlHTMLAttributes<ReactElement | ReactNode> {

}

export function DropDownOptions({ children }: DropDownOptionsProps): JSX.Element {
    const [isDropdownOpen, setDropdownOpen] = useState<boolean>(false);
    const dropdownRef = useRef<HTMLDivElement>(null);

    const handleOusideClick = (e: MouseEvent) => {
        if (dropdownRef && !dropdownRef.current?.contains(e.target as Node)) {
            setDropdownOpen(false);
        }
    }

    useEffect(() => {
        document.addEventListener('click', handleOusideClick, false);
        return () => document.removeEventListener('click', handleOusideClick, false);
    }, []);

    return (
        <div
            ref={dropdownRef}
            className={cn(css.dropDown, { [css.open]: isDropdownOpen })}
            onClick={(e) => {
                e.preventDefault();
                setDropdownOpen((isDropdownOpen) => !isDropdownOpen)
            }}
        >
            <div className={css.dropDown__icon}>
                <SvgIconDots />
            </div>
            <div className={css.dropDown__list}>
                {children}
            </div>
        </div>
    );
}