import React, { HtmlHTMLAttributes, JSX } from 'react';
import css from './Label.module.scss';
import cn from 'classnames';

interface LabelProps extends HtmlHTMLAttributes<HTMLElement> {}

export function Label( { children, className, ...props }: LabelProps ): JSX.Element {

    return (
        <label className={cn(css.label, className)} {...props}>
            {children}
        </label>
    );
}