import { IRootApi, RootApi } from "shared/api/RootApi";
import { IMe } from "../types/types";

export interface ILoginResponse extends IRootApi {
    data: {
        jwt: string;
    }
}

export interface IAuthMeResponse extends IRootApi {
    data: IMe;
}

interface IRegistrResponse {
    resultCode: 0 | 1 | 2;
}

export abstract class AuthApi extends RootApi {
    static async login(email: string, password: string) {
        return this.catchAxiosError<ILoginResponse>(async () => {
            let response = await this.axios.post('/auth/login', {
                email,
                password
            });
            return response;
        })
    }
    static async register(email: string, login: string, password: string) {
        return this.catchAxiosError<IRegistrResponse>(async () => {
            let response = await this.axios.post('/auth/register', {
                email,
                login,
                password
            });
            return response;
        })
    }

    static async me() {
        return this.catchAxiosError<IAuthMeResponse>(async () => {
            let response = await this.axios.get('/auth/me');
            let profileResponse = await this.axios.get('/profile/');
            if(profileResponse.status === 200) {
                response.data.data.photos = profileResponse.data.data.photos
            } else {
                response.data.data.photos = {
                    small: null,
                    large: null
                }
            }
            return response;
        })
    }
}