import { AppDispatch } from "shared/store/types";
import { setIsFetching, setIsFollow, setUsers } from "./slice";
import { UsersApi } from "../api/UsersApi";
import { store } from "app/store/store";

export const getUsersAction = () => async (dispath: AppDispatch) => {
    const { count, page, friend, term } = store.getState().usersReducer;
    dispath(setIsFetching(true));
    const response = await UsersApi.getUsers(count, page, friend, term);
    if(response && response.data.resultCode === 0) {
        dispath(setUsers(response.data.data.items));
    }
    dispath(setIsFetching(false));
}

export const followAction = (id: number, callback?: () => void) => async (dispath: AppDispatch) => {
    dispath(setIsFetching(true));
    const response = await UsersApi.follow(id);
    if(response && response.data.resultCode === 0) {
        if(callback) callback();
    }
    dispath(setIsFetching(false));
}

export const unFollowAction = (id: number, callback?: () => void) => async (dispath: AppDispatch) => {
    dispath(setIsFetching(true));
    const response = await UsersApi.unfollow(id);
    if(response && response.data.resultCode === 0) {
        if(callback) callback();
    }
    dispath(setIsFetching(false));
}

export const checkIsFollowAction = (id: number) => async (dispath: AppDispatch) => {
    dispath(setIsFetching(true));
    const response = await UsersApi.checkIsFollowed(id);
    if(response && response.data.resultCode === 0) {
        dispath(setIsFollow(response.data.data.follow));
    }
    dispath(setIsFetching(false));
}