import React, { Fragment, JSX, useEffect, useState } from 'react';
import css from './Profile.module.scss';
import cn from 'classnames';
import { Link, useParams } from 'react-router-dom';

import { ReactComponent as SvgIconSettings } from 'shared/assets/icons/settings.svg';
import { useAppDispatch, useAppSelector } from 'shared/store/hooks';
import { getProfileAction, selectIsFetching, selectProfile } from 'entities/profile';
import { SpinerOverlay } from 'shared/UI/Spiner/Spiner';
import ImageViewer from 'react-simple-image-viewer';
import { selectMe } from 'entities/auth';
import { Avatar, checkIsFollowAction } from 'entities/users';
import { selectIsFollow } from 'entities/users/store/selectors';
import { FollowUnfollw } from 'features/followUnfollw';
import { SendMessage } from 'features/SendMessage/SendMessage';
import { getText, selectLanguage } from 'features/Language';


export function Profile(): JSX.Element {
    const [isViewerOpen, setIsViewerOpen] = useState<boolean>(false);
    const currentLanguage = useAppSelector(selectLanguage);
    const isFetching = useAppSelector(selectIsFetching);
    const profile = useAppSelector(selectProfile);
    const isFollow = useAppSelector(selectIsFollow);
    const me = useAppSelector(selectMe);
    const urlParams = useParams();
    const id = urlParams.id ?? me?.id;

    const dispatch = useAppDispatch();
    const isOwner = me?.id === profile?.userId;

    const contacts = profile
        ? Object.entries(profile.contacts)
            .filter(
                item =>
                    item[0] !== 'profileId'
                    && item[1] !== null
                    && (typeof item[1] === 'string')
                    && item[1].trim() !== ''
            )
        : [];


    useEffect(() => {
        if (id) {
            dispatch(checkIsFollowAction(Number(id)));
            dispatch(getProfileAction(Number(id)));
        }
    }, [id])

    return (
        <div className={css.profile}>
            {!isFetching && profile
                ? <>
                    <div className={css.profile__head}>
                        <div className={css.profile__photo} onClick={() => setIsViewerOpen(true)}>
                            <Avatar src={profile.photos.large} />
                        </div>
                        {isViewerOpen && profile.photos.large &&
                            <ImageViewer
                                src={[String(profile.photos.large)]}
                                backgroundStyle={{ zIndex: 10 }}
                                onClose={() => setIsViewerOpen(false)}
                            />
                        }
                        {!isOwner &&
                            <>
                                <div className="d-flex gap-3 mt-3">
                                    <SendMessage userId={profile.userId} />
                                    {(isFollow !== null) &&
                                        <FollowUnfollw
                                            id={Number(id)}
                                            isFollow={isFollow}
                                            callbackAfterFetch={() => dispatch(checkIsFollowAction(Number(id)))}
                                        />
                                    }
                                </div>
                            </>
                        }
                    </div>
                    <div className={cn(css.profile__body, 'mt-3 mt-md-0')}>
                        {isOwner &&
                            <Link className={css.profile__conf} to='/profile/configuration'>
                                <SvgIconSettings />
                            </Link>
                        }
                        <h4 className={css.profile__name}>{profile.fullName}</h4>

                        {profile.status && <div className='mt-3'>
                            <h6>
                                {getText(currentLanguage, {
                                    en: 'Status',
                                    ua: 'Статус'
                                })}
                            </h6>
                            {profile.status}
                        </div>}

                        {profile.lookingForAJob && <div className='mt-3'>
                            <h6>
                                {getText(currentLanguage, {
                                    en: 'Looking For a Job',
                                    ua: 'Шукаю роботу'
                                })}
                            </h6>
                            {profile.lookingForAJobDescription}
                        </div>}

                        {!!contacts.length && <div className="mt-3">
                            <h6>
                                {getText(currentLanguage, {
                                    en: 'Contacts',
                                    ua: 'Контакти'
                                })}
                            </h6>
                            <table className={css.profile__contacts}>
                                <tbody>
                                    {contacts.map(item => {
                                        return (
                                            <Fragment key={item[0]}>
                                                {item[1] &&
                                                    <tr>
                                                        <td><strong>{item[0].charAt(0).toUpperCase() + item[0].slice(1)}:</strong></td>
                                                        <td>{item[1]}</td>
                                                    </tr>
                                                }
                                            </Fragment>
                                        );
                                    })}
                                </tbody>
                            </table>
                        </div>}

                    </div>
                </>
                : <SpinerOverlay size={1.5} />
            }

        </div>
    );
}