import { combineReducers, configureStore } from "@reduxjs/toolkit";
import { profileSliceReducer } from "entities/profile";
import { authSliceReducer } from "entities/auth";
import { usersSliceReducer } from "entities/users";
import { popupMessageSliceReducer } from "entities/popupMessage";
import { dialogsSliceReducer } from "entities/dialogs";
import { messagesSliceReducer } from "entities/messages";
import { languageSliceReducer } from "features/Language";

export const rootReducer = combineReducers({
    authReducer: authSliceReducer,
    usersReducer: usersSliceReducer,
    profileReducer: profileSliceReducer,
    popupMessageReducer: popupMessageSliceReducer,
    dialogsReducer: dialogsSliceReducer,
    messagesReducer: messagesSliceReducer,
    languageReducer: languageSliceReducer,
});

export const store = configureStore({
    reducer: rootReducer,
});
