import { log } from "console";
import { useState } from "react";

type ThemeState = 'dark' | 'light';



export function useTheme() {
    (localStorage.getItem('theme') === null) && localStorage.setItem('theme', 'light');
    const themeState = localStorage.getItem('theme') as ThemeState;

    const [theme, setThemeState] = useState<ThemeState>(themeState);

    const setTheme = (state: ThemeState) => {
        localStorage.setItem('theme', state);
        state === 'dark'
            ? document.documentElement.classList.add('themeDark')
            : document.documentElement.classList.remove('themeDark')
    }
    setTheme(theme);

    const toggleTheme = () => {
        theme === 'dark' ? setThemeState('light') : setThemeState('dark');
    }

    return [theme, toggleTheme] as [theme: ThemeState, toggleTheme: () => void];
}