import React, { JSX, useState } from 'react';
import css from './RemoveMemberFromDialog.module.scss';
import cn from 'classnames';
import { ReactComponent as SvgIconClose } from 'shared/assets/icons/close.svg';
import { useAppDispatch, useAppSelector } from 'shared/store/hooks';
import { removeUserFromDialogActions } from 'entities/dialogs';
import { createPortal } from 'react-dom';
import { PopUpMessage } from 'entities/popupMessage';
import { Button } from 'shared/UI/Button/Button';
import { getText, selectLanguage } from 'features/Language';

interface RemoveMemberFromDialogProps {
    dialogId: number;
    userId: number;
    onRemoved: () => void;
}

export function RemoveMemberFromDialog({ dialogId, userId, onRemoved }: RemoveMemberFromDialogProps): JSX.Element {
    const [isConfirmOpen, setConfirmOpen] = useState<boolean>(false);
    const dispatch = useAppDispatch();
    const currentLanguage = useAppSelector(selectLanguage);

    const removeMemberHandle = () => {
        dispatch(removeUserFromDialogActions(dialogId, userId));
        onRemoved();
    }
    return (
        <>
            <button onClick={() => setConfirmOpen(true)} className={css.delete}>
                <SvgIconClose />
            </button>
            {
                createPortal(
                    <PopUpMessage
                        toggleShow={isConfirmOpen}
                        onClose={(e) => {
                            e.preventDefault();
                            setConfirmOpen(false)
                        }}
                    >

                        {
                            getText(currentLanguage, {
                                en: 'Are you sure you want to remove member from dialog?',
                                ua: 'Ви впевнені що хочете видалити учасника з діалогу?'
                            })
                        }
                        {
                            <div className="d-flex justify-content-center mt-3 gap-3">
                                <Button
                                    variant='danger'
                                    onClick={(e) => {
                                        e.preventDefault();
                                        removeMemberHandle();
                                        setConfirmOpen(false);
                                    }}
                                >
                                    {
                                        getText(currentLanguage, {
                                            en: 'Yes',
                                            ua: 'Так'
                                        })
                                    }
                                </Button>
                                <Button onClick={(e) => {
                                    e.preventDefault();
                                    setConfirmOpen(false);
                                }}>
                                    {
                                        getText(currentLanguage, {
                                            en: 'No',
                                            ua: 'Ні'
                                        })
                                    }
                                </Button>
                            </div>
                        }
                    </PopUpMessage>,

                    document.body
                )
            }
        </>
    );
}