import React, { JSX } from 'react';
import cn from 'classnames';
import { useAppDispatch, useAppSelector } from 'shared/store/hooks';
import { Button } from 'shared/UI/Button/Button';
import { followAction, unFollowAction } from 'entities/users';
import { getText, selectLanguage } from 'features/Language';

interface FollowUnfollwProps {
    id: number;
    isFollow: boolean;
    callbackAfterFetch?: () => void;
}

export function FollowUnfollw({ id, isFollow, callbackAfterFetch }: FollowUnfollwProps): JSX.Element {
    const dispatch = useAppDispatch();
    const currentLanguage = useAppSelector(selectLanguage);
    return (
        <>
            {isFollow
                ? <Button
                    onClick={() => dispatch(unFollowAction(id, callbackAfterFetch))}
                    size={0.8}
                >
                    {
                        getText(currentLanguage, {
                            en: 'Unfollow',
                            ua: 'Видалити з друзів'
                        })
                    }
                </Button>
                : <Button
                    onClick={() => dispatch(followAction(id, callbackAfterFetch))}
                    size={0.8}
                >
                    {
                        getText(currentLanguage, {
                            en: 'Follow',
                            ua: 'Додати в друзі'
                        })
                    }
                </Button>
            }
        </>
    );
}
