import React, { JSX } from 'react';
import { Dialogs } from 'widgets/Dialogs';
import { Header } from 'widgets/Header';



export function DialogsPage(): JSX.Element {
    return (
        <>
            <Header />
            <div className='container p-2 p-md-3'>
                <Dialogs />
            </div>
        </>
    );
}