import { RootState } from "shared/store/types";
import { IUser } from "../types/types";

export const selectUsers = (state: RootState): IUser[] => state?.usersReducer?.users || [];
export const selectIsFetching = (state: RootState): boolean => state?.usersReducer?.isFetching || false;
export const selectTotalCount = (state: RootState): number => state?.usersReducer?.totalCount || 0;
export const selectFriend = (state: RootState): boolean => state?.usersReducer?.friend || false;
export const selectTerm = (state: RootState): string => state?.usersReducer?.term || '';
export const selectIsFollow = (state: RootState): boolean | null => {
    return state?.usersReducer?.isFollow === undefined ? null : state?.usersReducer?.isFollow;
}