import React from "react";
import { DialogsPage, LogInPage, MessagesPage, ProfileConfigPage, ProfilePage, RegistrPage, UsersPage } from "pages";
import { Navigate, RouteProps } from "react-router-dom";


export const privateRoutes: RouteProps[] = [
    { path: '/profile', element: <ProfilePage /> },
    { path: '/profile/:id', element: <ProfilePage /> },
    { path: '/profile/configuration', element: <ProfileConfigPage /> },
    { path: '/users', element: <UsersPage /> },
    { path: '/dialogs', element: <DialogsPage /> },
    { path: '/messages/:id', element: <MessagesPage /> },
    { path: '*', element: <Navigate to="/profile" replace /> },
];

export const publicRoutes: RouteProps[] = [
    { path: '/login', element: <LogInPage /> },
    { path: '/registr', element: <RegistrPage /> },
    { path: '*', element: <Navigate to="/login" replace /> },
];