import { IRootApi, RootApi } from "shared/api/RootApi";
import { IProfile, IProfilePhotos, IUpdateProfile } from "../types/types";

interface IGetProfileResponse extends IRootApi {
    data: IProfile;
}

interface IUploadPhotoResponse extends IRootApi {
    data: IProfilePhotos;
}

export abstract class ProfileApi extends RootApi {
    static async getProfile(id = '') {
        return this.catchAxiosError<IGetProfileResponse>(async () => {
            let response = await this.axios.get('/profile/' + id);
            return response;
        })
    }
    static async updateProfile(data: IUpdateProfile) {
        return this.catchAxiosError<IGetProfileResponse>(async () => {
            let response = await this.axios.put('/profile', {
                ...data
            });
            return response;
        })
    }
    static async uploadPhoto(file: File) {
        const formData = new FormData();
        formData.append('photo', file);
        return this.catchAxiosError<IUploadPhotoResponse>(async () => {
            let response = await this.axios.put('/profile/photo', formData, {
                headers: {
                    'Content-Type': `multipart/form-data`,
                },
            });
            return response;
        })
    }
}