import { IRootApi, RootApi } from "shared/api/RootApi";
import { IUser } from "../types/types";

interface IFollowUnfollowResponse extends IRootApi {
    data: {
        message: string;
    }
}

interface IGetUsersResponse extends IRootApi {
    data: {
        items: IUser[];
        totalCount: number;
    }
}

interface ICheckIsFollowedResponse extends IRootApi {
    data: {
        follow: boolean;
    }
}

interface IParams {
    count: number;
    page: number;
    friend: boolean;
    term?: string;
}

export abstract class UsersApi extends RootApi {
    static async getUsers(count: number, page: number, friend: boolean, term?: string) {
        return this.catchAxiosError<IGetUsersResponse>(async () => {
            let params: IParams = {
                count, 
                page, 
                friend,
            }
    
            if(term?.trim()) {
                params.term = term;
            }
            
            let response = await this.axios.get('/users', {
                params
            });
            return response;
        })
    }
    static async follow(id: number) {
        return this.catchAxiosError<IFollowUnfollowResponse>(async () => {
            let response = await this.axios.post('/follow/' + id);
            return response;
        })
    }
    static async unfollow(id: number) {
        return this.catchAxiosError<IFollowUnfollowResponse>(async () => {
            let response = await this.axios.delete('/follow/' + id);
            return response;
        })
    }
    static async checkIsFollowed(id: number) {
        return this.catchAxiosError<ICheckIsFollowedResponse>(async () => {
            let response = await this.axios.get('/follow/' + id);
            return response;
        })
    }
}