import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { IMe, RegistrStatus } from '../types/types';

interface AuthState {
    isAuth: boolean;
    isAuthCompleted: boolean;
    me: null | IMe;
    isFetching: boolean;
    isLoginError: boolean;
    isRegistrError: boolean;
    registrErrorText: string;
    registrStatus: RegistrStatus;
}

const initialState: AuthState = {
    isAuth: false,
    isAuthCompleted: false,
    me: null,
    isFetching: false,
    isLoginError: false,
    isRegistrError: false,
    registrErrorText: '',
    registrStatus: null
}

export const authSlice = createSlice({
    name: 'auth',
    initialState,
    reducers: {
        setIsAuth(state, action: PayloadAction<boolean>) {
            state.isAuth = action.payload;
        },
        setIsAuthCompleted(state, action: PayloadAction<boolean>) {
            state.isAuthCompleted = action.payload;
        },
        setMe(state, action: PayloadAction<IMe>) {
            state.me = action.payload;
        },
        setIsFetching(state, action: PayloadAction<boolean>) {
            state.isFetching = action.payload;
        },
        setIsLoginError(state, action: PayloadAction<boolean>) {
            state.isLoginError = action.payload;
        },
        setIsRegistrError(state, action: PayloadAction<boolean>) {
            state.isRegistrError = action.payload;
        },
        setRegistrErrorText(state, action: PayloadAction<string>) {
            state.registrErrorText = action.payload
        },
        setRegistrStatus(state, action: PayloadAction<RegistrStatus>) {
            state.registrStatus = action.payload
        },
    }
});

export const {
    setIsAuth,
    setIsAuthCompleted,
    setMe,
    setIsFetching,
    setIsLoginError,
    setIsRegistrError,
    setRegistrErrorText,
    setRegistrStatus,
} = authSlice.actions;

export const authSliceReducer = authSlice.reducer;