import React, { HtmlHTMLAttributes, JSX, ReactElement, ReactNode } from 'react';
import css from './Box.module.scss';
import cn from 'classnames';

interface BoxProps extends HtmlHTMLAttributes<Element> {
    children?: ReactElement | ReactNode;
}

export function Box( { children, className, ...props }: BoxProps ): JSX.Element {
    return (
        <div className={cn(css.box, className)} {...props}>
            {children}
        </div>
    );
}