import React, { JSX } from 'react';
import cn from 'classnames';
import { useAppDispatch, useAppSelector } from 'shared/store/hooks';
import { IDialog } from 'entities/dialogs/types/types';
import { createDialogActions, getDialogsActions } from 'entities/dialogs';
import { useNavigate } from 'react-router-dom';
import { Button } from 'shared/UI/Button/Button';
import { getText, selectLanguage } from 'features/Language';

interface SendMessageProps {
    userId: number
}

export function SendMessage({ userId }: SendMessageProps): JSX.Element {
    const dispatch = useAppDispatch();
    const navigate = useNavigate();
    const currentLanguage = useAppSelector(selectLanguage);

    const onSendMessageHandler = (userId: number) => {
        const dialogs = new Promise<IDialog[]>(res => {
            dispatch(getDialogsActions(res));
        })
        dialogs
            .then(dialogs => {
                const dialog = dialogs.find(dialog => dialog.members.includes(String(userId)));

                if (dialog) {
                    navigate('/messages/' + dialog.id);
                } else {
                    dispatch(createDialogActions(userId, (id) => navigate('/messages/' + id)));
                }
            })
    }
    return (
        <Button onClick={() => onSendMessageHandler(userId)} size={0.8}>
            {
                getText(currentLanguage, {
                    en: 'Send Message',
                    ua: 'Відправити повідомлення'
                })
            }
        </Button>
    );
}