import React, { JSX, useEffect } from 'react';
import css from './RegistrPage.module.scss';
import cn from 'classnames';

import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from "yup";

import { FlexCenterBox } from 'shared/UI/FlexCenterBox/FlexCenterBox';
import { Box } from 'shared/UI/Box/Box';
import { Input, InputPassword } from 'shared/UI/Input/Input';
import { Button } from 'shared/UI/Button/Button';
import { Link, useNavigate } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from 'shared/store/hooks';
import { registrAction } from 'entities/auth/store/actions';
import { PopUpMessage, selectPopupMessageText, selectTogglePopupMessageShow, setPopupMessageText, setTogglePopupMessageShow } from 'entities/popupMessage';
import { selectIsFetching, selectIsRegistrError, selectRegistrErrorText, selectRegistrStatus } from 'entities/auth';
import { SpinerOverlay } from 'shared/UI/Spiner/Spiner';

const getCharacterValidationError = (str: string): string => {
    return `Your password must have at least 1 ${str} character`;
};

interface FormData {
    email: string;
    login: string;
    password: string;
    confirm_password: string;
}

const schema = yup.object().shape({
    email: yup.string().email().required(),
    login: yup.string().min(2).required(),
    password: yup.string()
        .matches(/[a-zA-Z0-9]/, 'Password can only contain Latin letters and numbers.')
        .matches(/[0-9]/, getCharacterValidationError("digit"))
        .matches(/[a-z]/, getCharacterValidationError("lowercase"))
        .matches(/[A-Z]/, getCharacterValidationError("uppercase"))
        .matches(/[#|$|%|*]/, getCharacterValidationError("special # $ % *"))
        .min(8, "Password must have at least 8 characters")
        .required(),
    confirm_password: yup.string().oneOf([yup.ref('password')], 'Password does not match').required()
}).required();

export function RegistrPage(): JSX.Element {
    const navigate = useNavigate();
    const isFetching = useAppSelector(selectIsFetching);
    const isRegistrError = useAppSelector(selectIsRegistrError);
    const registrErrorText = useAppSelector(selectRegistrErrorText);
    const registrStatus = useAppSelector(selectRegistrStatus);
    const togglePopupMessageShow = useAppSelector(selectTogglePopupMessageShow);
    const popupMessageText = useAppSelector(selectPopupMessageText);
    const dispatch = useAppDispatch();
    const { register, handleSubmit, setError, formState: { errors } } = useForm<FormData>({
        resolver: yupResolver(schema),
        mode: 'onSubmit'
    });

    useEffect(() => {
        if (registrStatus === 1) {
            setError('email', {})
        } else if (registrStatus === 2) {
            setError('email', {})
            setError('login', {})
            setError('password', {})
            setError('confirm_password', {})
        }
    }, [registrStatus])

    const onSubmit = (data: FormData) => {
        const onSuccess = () => {
            dispatch(setPopupMessageText('New user created!'));
            dispatch(setTogglePopupMessageShow(true));

            setTimeout(() => {
                dispatch(setTogglePopupMessageShow(false));
                navigate('/login');
            }, 3000)
        }

        dispatch(registrAction(
            data.email,
            data.login,
            data.password,
            {
                userExistsMessage: 'User with this email already exists.',
                wrongDataMessage: 'Invalid email, login or password',
            },
            onSuccess
        ));
    }
    //fdfdfdfaA#2
    return (
        <>
            {isFetching && <SpinerOverlay size={1.5} />}
            <FlexCenterBox style={{ minHeight: '100svh' }}>
                <Box style={{ maxWidth: '60rem', width: '100%' }}>
                    <form onSubmit={handleSubmit(onSubmit)}>
                        <h3>Registration</h3>
                        {isRegistrError && <h5 className='mt-3 text-color-error'>{registrErrorText}</h5>}
                        <div className="mt-3">
                            <Input
                                type="text"
                                defaultValue='lena@gmailcom'
                                placeholder="Email"
                                {...register('email')}
                                errorState={errors.email ? true : false}
                            />
                            {errors.email && <div className="mt-2 text-size-2 text-color-error">{errors.email.message}</div>}
                        </div>
                        <div className="mt-3">
                            <Input
                                type="text"
                                defaultValue='Lena'
                                placeholder="Login"
                                {...register('login')}
                                errorState={errors.login ? true : false}
                            />
                            {errors.login && <div className="mt-2 text-size-2 text-color-error">{errors.login.message}</div>}
                        </div>
                        <div className="mt-3">
                            <InputPassword
                                defaultValue='fdfdfdfaA#2'
                                placeholder="Password"
                                {...register('password')}
                                errorState={errors.password ? true : false}
                            />
                            {errors.password && <div className="mt-2 text-size-2 text-color-error">{errors.password.message}</div>}
                        </div>
                        <div className="mt-3">
                            <InputPassword
                                defaultValue='fdfdfdfaA#2'
                                placeholder="Confirm password"
                                {...register('confirm_password')}
                                errorState={errors.confirm_password ? true : false}
                            />
                            {errors.confirm_password && <div className="mt-2 text-size-2 text-color-error">{errors.confirm_password.message}</div>}
                        </div>
                        <div className="mt-3 d-flex gap-3 align-items-center justify-content-between">
                            <Button>Registration</Button>
                            <Link to='/login'>Log in</Link>
                        </div>
                    </form>
                </Box>
            </FlexCenterBox>
            <PopUpMessage
                toggleShow={togglePopupMessageShow}
                onClose={() => dispatch(setTogglePopupMessageShow(false))}
            >
                {popupMessageText}
            </PopUpMessage>
        </>
    );
}