import React, { CSSProperties, JSX, ReactNode } from 'react';
import css from './List.module.scss';
import cn from 'classnames';

interface ListProps<T> {
    items: T[];
    renderItem: (item: T, ...props: any[]) => ReactNode;
    emptyState: () => ReactNode;
}

export function List<T>({ items, renderItem, emptyState }: ListProps<T>) {
    return (
        <div className={css.list} >
            {!!items.length
                ? items.map((item, index) => {
                    const style = { zIndex: items.length - index } as CSSProperties;
                    return renderItem(item, style)
                })
                : emptyState()
            }
        </div>
    );
}