import { AppDispatch } from "shared/store/types";
import { setIsFetching, setProfile } from "./slice";
import { ProfileApi } from "../api/ProfileApi";
import { IProfile, IUpdateProfile } from "../types/types";

interface IUpdateProfileMessages {
    successMessage: string;
    errorMessage: string;
}

export const getProfileAction = (id?: number, next?: (profile: IProfile) => void) => async (dispatch: AppDispatch) => {
    dispatch(setIsFetching(true));
    const response = id ? await ProfileApi.getProfile(String(id)) : await ProfileApi.getProfile();
    if (response && response.data.resultCode === 0) {
        dispatch(setProfile(response.data.data));
        if(next) next(response.data.data);
    }
    dispatch(setIsFetching(false));
}

export const updateProfileAction = (
    data: IUpdateProfile,
    callbackAfterSuccessFetch: () => void,
    callbackAfterUnsuccessFetch: () => void,
) => async (dispatch: AppDispatch) => {
    dispatch(setIsFetching(true));
    const response = await ProfileApi.updateProfile(data);
    if (response && response.data.resultCode === 0) {
        dispatch(setProfile(response.data.data));
        callbackAfterSuccessFetch();
        // dispatch(setPopupMessageText(statusMessages.successMessage));
        // dispatch(setTogglePopupMessageShow(true));

        // setTimeout(() => {
        //     dispatch(setTogglePopupMessageShow(false));
        // }, 3000)
    } else {
        callbackAfterUnsuccessFetch();
        // dispatch(setPopupMessageText(statusMessages.errorMessage));
        // dispatch(setTogglePopupMessageShow(true));

        // setTimeout(() => {
        //     dispatch(setTogglePopupMessageShow(false));
        // }, 3000)
    }
    dispatch(setIsFetching(false));
}

export const uploadPhotoAction = (
    file: File,
    callbackAfterSuccessFetch: () => void,
    callbackAfterUnsuccessFetch: () => void,
) => async (dispatch: AppDispatch) => {
    dispatch(setIsFetching(true));
    const response = await ProfileApi.uploadPhoto(file);
    if (response && response.data.resultCode === 0) {
        dispatch(getProfileAction());
        callbackAfterSuccessFetch();

        // dispatch(setPopupMessageText(statusMessages.successMessage));
        // dispatch(setTogglePopupMessageShow(true));
        // setTimeout(() => {
        //     dispatch(setTogglePopupMessageShow(false));
        // }, 3000)
    } else {
        callbackAfterUnsuccessFetch();
        // dispatch(setPopupMessageText(statusMessages.errorMessage));
        // dispatch(setTogglePopupMessageShow(true));

        // setTimeout(() => {
        //     dispatch(setTogglePopupMessageShow(false));
        // }, 3000)
    }
    dispatch(setIsFetching(false));
}