import React, { JSX, useEffect } from 'react';
import css from './ProfileConfig.module.scss';
import cn from 'classnames';

import {
    getProfileAction,
    selectIsFetching,
    selectProfile,
} from 'entities/profile';
import { selectMe } from 'entities/auth';
import { useAppDispatch, useAppSelector } from 'shared/store/hooks';
import { SpinerOverlay } from 'shared/UI/Spiner/Spiner';
import { UpdateProfileContactsForm } from 'features/updateProfileContactsForm';
import { UploadProfilePhotoForm } from 'features/uploadProfilePhotoForm';
import { PopUpMessage, selectPopupMessageText, selectTogglePopupMessageShow, setTogglePopupMessageShow } from 'entities/popupMessage';
import { getText, selectLanguage } from 'features/Language';


export function ProfileConfig(): JSX.Element {
    const profile = useAppSelector(selectProfile);
    const isFetching = useAppSelector(selectIsFetching);
    const togglePopupMessageShow = useAppSelector(selectTogglePopupMessageShow);
    const popupMessageText = useAppSelector(selectPopupMessageText);
    const me = useAppSelector(selectMe);
    const dispatch = useAppDispatch();
    const currentLanguage = useAppSelector(selectLanguage);

    useEffect(() => {
        if (me) {
            dispatch(getProfileAction(me.id));
        }
    }, []);

    return (
        <>
            {isFetching && <SpinerOverlay size={1.5} />}
            <div className={css.profile_config}>
                <h2>
                    {getText(currentLanguage, {
                        en: 'Configuration',
                        ua: 'Конфігурація'
                    })}
                </h2>
                {profile
                    ? <>
                        <div className="mt-3">
                            <UpdateProfileContactsForm />
                        </div>
                        <div className="mt-3">
                            <UploadProfilePhotoForm />
                        </div>
                    </>
                    : <SpinerOverlay size={1.5} />
                }
            </div>
            <PopUpMessage
                toggleShow={togglePopupMessageShow}
                onClose={() => dispatch(setTogglePopupMessageShow(false))}
            >
                {popupMessageText}
            </PopUpMessage>
        </>
    );
}