import React, { JSX, ReactNode } from 'react';
import css from './UserCard.module.scss';
import cn from 'classnames';
import { IUser } from 'entities/users/types/types';
import { Box } from 'shared/UI/Box/Box';
import { Link } from 'react-router-dom';
import { CircleAvatar } from '../CircleAvatar/CircleAvatar';

interface UserCardProps {
    user: IUser;
    bottomSlot?: () => ReactNode;
}

export function UserCard({ user, bottomSlot }: UserCardProps): JSX.Element {
    return (
        <Box>
            <div className={css.card}>
                <div className={css.card__row_1}>
                    <div className={css.card__photo}>
                        <Link to={`/profile/${user.id}`}>
                            <CircleAvatar src={user.photos.small} />
                        </Link>
                    </div>
                    <div className={css.card__text}>
                        <h6 className='mb-2'>{user.login}</h6>
                        {user.status}
                    </div>
                </div>
                {bottomSlot &&
                    <div className={css.card__row_2}>
                        {bottomSlot()}
                    </div>
                }
            </div>
        </Box>
    );
}
