import React, { JSX } from 'react';
import css from './UpdateProfileContactsForm.module.scss';
import cn from 'classnames';
import { useForm } from 'react-hook-form';
import { useAppDispatch, useAppSelector } from 'shared/store/hooks';
import { selectProfile, updateProfileAction } from 'entities/profile';
import { Box } from 'shared/UI/Box/Box';
import { Input } from 'shared/UI/Input/Input';
import { Label } from 'shared/UI/Label/Label';
import { Button } from 'shared/UI/Button/Button';
import { setPopupMessageText, setTogglePopupMessageShow } from 'entities/popupMessage';
import { getText, selectLanguage } from 'features/Language';

interface ContactFormData {
    fullName: string;
    status: string;
    lookingForAJobDescription: string;
    facebook: string;
    github: string;
    instagram: string;
    twitter: string;
    youtube: string;
    website: string;
}


export function UpdateProfileContactsForm(): JSX.Element {
    const contactForm = useForm<ContactFormData>();
    const profile = useAppSelector(selectProfile);
    const dispatch = useAppDispatch();
    const currentLanguage = useAppSelector(selectLanguage);

    const onContactFormSubmit = (data: ContactFormData) => {
        const newData = {
            fullName: data.fullName.trim(),
            status: data.status.trim(),
            lookingForAJob: !!data.lookingForAJobDescription.trim(),
            lookingForAJobDescription: data.lookingForAJobDescription.trim(),
            contacts: {
                facebook: data.facebook.trim(),
                github: data.github.trim(),
                instagram: data.instagram.trim(),
                twitter: data.twitter.trim(),
                youtube: data.youtube.trim(),
                website: data.website.trim()
            }
        }

        const onSuccess = () => {
            dispatch(setPopupMessageText('Update was success!'));
            dispatch(setTogglePopupMessageShow(true));

            setTimeout(() => {
                dispatch(setTogglePopupMessageShow(false));
            }, 3000)
        }
        const onUnsuccess = () => {
            dispatch(setPopupMessageText('Something went wrong, please try again later.'));
            dispatch(setTogglePopupMessageShow(true));

            setTimeout(() => {
                dispatch(setTogglePopupMessageShow(false));
            }, 3000)
        }

        dispatch(updateProfileAction(
            newData,
            onSuccess,
            onUnsuccess
        ));
    }

    return (
        <Box>
            {profile &&
                <form onSubmit={contactForm.handleSubmit(onContactFormSubmit)}>
                    <div>
                        <h6 className='mb-2'>
                            {getText(currentLanguage, {
                                en: 'Full Name',
                                ua: 'Повне Ім\'я'
                            })}
                        </h6>
                        <Input
                            defaultValue={profile.fullName}
                            {...contactForm.register('fullName')}
                            placeholder='Full Name'
                            errorState={contactForm.formState.errors.fullName ? true : false} />
                    </div>
                    <div className='mt-3'>
                        <h6 className='mb-2'>
                            {getText(currentLanguage, {
                                en: 'Status',
                                ua: 'Статус'
                            })}
                        </h6>
                        <Input
                            defaultValue={profile?.status ? profile?.status : undefined}
                            {...contactForm.register('status')}
                            placeholder='Status'
                            errorState={contactForm.formState.errors.status ? true : false} />
                    </div>
                    <div className='mt-3'>
                        <h6 className='mb-2'>
                            {getText(currentLanguage, {
                                en: 'Looking for job',
                                ua: 'Шукаю роботу'
                            })}
                        </h6>
                        <Input
                            defaultValue={profile?.lookingForAJobDescription ? profile?.lookingForAJobDescription : undefined}
                            {...contactForm.register('lookingForAJobDescription')}
                            placeholder='LookingForAJobDescription'
                            errorState={contactForm.formState.errors.lookingForAJobDescription ? true : false} />
                    </div>
                    <h6 className='mt-4'>
                        {getText(currentLanguage, {
                            en: 'Contacts',
                            ua: 'Контакти'
                        })}
                    </h6>
                    <div className='mt-3'>
                        <Label className='mb-1'>Facebook</Label>
                        <Input
                            defaultValue={profile?.contacts?.facebook ? profile?.contacts?.facebook : undefined}
                            {...contactForm.register('facebook')}
                            placeholder='Facebook'
                            errorState={contactForm.formState.errors.facebook ? true : false} />
                    </div>
                    <div className='mt-3'>
                        <Label className='mb-1'>Github</Label>
                        <Input
                            defaultValue={profile?.contacts?.github ? profile?.contacts?.github : undefined}
                            {...contactForm.register('github')}
                            placeholder='Github'
                            errorState={contactForm.formState.errors.github ? true : false} />
                    </div>
                    <div className='mt-3'>
                        <Label className='mb-1'>Instagram</Label>
                        <Input
                            defaultValue={profile?.contacts?.instagram ? profile?.contacts?.instagram : undefined}
                            {...contactForm.register('instagram')}
                            placeholder='Instagram'
                            errorState={contactForm.formState.errors.instagram ? true : false} />
                    </div>
                    <div className='mt-3'>
                        <Label className='mb-1'>Twitter</Label>
                        <Input
                            defaultValue={profile?.contacts?.twitter ? profile?.contacts?.twitter : undefined}
                            {...contactForm.register('twitter')}
                            placeholder='Twitter'
                            errorState={contactForm.formState.errors.twitter ? true : false} />
                    </div>
                    <div className='mt-3'>
                        <Label className='mb-1'>Youtube</Label>
                        <Input
                            defaultValue={profile?.contacts?.youtube ? profile?.contacts?.youtube : undefined}
                            {...contactForm.register('youtube')}
                            placeholder='Youtube'
                            errorState={contactForm.formState.errors.youtube ? true : false} />
                    </div>
                    <div className='mt-3'>
                        <Label className='mb-1'>Website</Label>
                        <Input
                            defaultValue={profile?.contacts?.website ? profile?.contacts?.website : undefined}
                            {...contactForm.register('website')}
                            placeholder='Website'
                            errorState={contactForm.formState.errors.website ? true : false} />
                    </div>
                    <div className="mt-3">
                        <Button disabled={!contactForm.formState.isDirty}>
                            {getText(currentLanguage, {
                                en: 'Save',
                                ua: 'Зберегти'
                            })}
                        </Button>
                    </div>
                </form>
            }
        </Box>
    );
}