import { createSlice, PayloadAction } from '@reduxjs/toolkit';

interface PopupMessageState {
    togglePopupMessageShow: boolean;
    popupMessageText: string;
}

const initialState: PopupMessageState = {
    togglePopupMessageShow: false,
    popupMessageText: '',
}

export const popupMessageSlice = createSlice({
    name: 'popupMessage',
    initialState,
    reducers: {
        setTogglePopupMessageShow(state, action: PayloadAction<boolean>) {
            state.togglePopupMessageShow = action.payload;
        },
        setPopupMessageText(state, action: PayloadAction<string>) {
            state.popupMessageText = action.payload;
        }
    }
});

export const {
    setTogglePopupMessageShow,
    setPopupMessageText,
} = popupMessageSlice.actions;

export const popupMessageSliceReducer = popupMessageSlice.reducer;