import React, { HtmlHTMLAttributes, JSX, MouseEvent ,ReactElement, ReactNode, useRef } from 'react';
import css from './PopUpMessage.module.scss';
import cn from 'classnames';
import { ReactComponent as SvgIconCLose } from './close.svg';
import { CSSTransition } from 'react-transition-group';

interface PopUpMessageProps extends HtmlHTMLAttributes<ReactElement | ReactNode> {
    onClose: (e: MouseEvent<HTMLButtonElement>) => void;
    toggleShow: boolean;
}

export function PopUpMessage({ children, toggleShow, onClose }: PopUpMessageProps): JSX.Element {
    const nodeRef = useRef(null);
    return (
        <CSSTransition
            in={toggleShow}
            nodeRef={nodeRef}
            timeout={300}
            mountOnEnter
            unmountOnExit
            classNames={{
                enter: css.popup_message_enter,
                enterActive: css.popup_message_enter_active,
                exit: css.popup_message_exit,
                exitActive: css.popup_message_exit_active
            }}
        >
            <div ref={nodeRef} className={css.popup_message}>
                <div className={css.popup_message__container}>
                    <div className={css.popup_message__alert}>
                        <button className={css.popup_message__close} onClick={onClose}>
                            <SvgIconCLose />
                        </button>
                        {children}
                    </div>
                </div>
            </div>

        </CSSTransition>
    );
}
