import React, { JSX } from 'react';
import css from './Members.module.scss';
import cn from 'classnames';
import { IProfile } from 'entities/profile/types/types';
import { useAppSelector } from 'shared/store/hooks';
import { Link, useNavigate } from 'react-router-dom';
import { selectMe } from 'entities/auth';
import { CSSTransition } from 'react-transition-group';
import { Button } from 'shared/UI/Button/Button';

import { ReactComponent as SvgIconPlusUsers } from 'shared/assets/icons/plus-user.svg';
import { Box } from 'shared/UI/Box/Box';
import { CircleAvatar } from 'entities/users';
import { RemoveMemberFromDialog } from 'features/RemoveMemberFromDialog/RemoveMemberFromDialog';
import { getText, selectLanguage } from 'features/Language';

interface MembersProps {
    isShow: boolean;
    profiles: IProfile[];
    setProfiles: (profiles: IProfile[]) => void;
    dialogId: number;
}
export function Members({ isShow, profiles, setProfiles, dialogId }: MembersProps): JSX.Element {
    const me = useAppSelector(selectMe);
    const navigate = useNavigate();
    const currentLanguage = useAppSelector(selectLanguage);

    const addNewUserHandler = () => {
        navigate('/users', {
            state: {
                dialogId,
                currentMembers: profiles,
            }
        })
    }

    const onRemoveMemberFromDialogHandler = (userId: number) => {
        const filteredProfiles = profiles.filter(profile => profile.userId !== userId);
        setProfiles(filteredProfiles);
    }

    return (
        <CSSTransition
            in={isShow}
            timeout={300}
            mountOnEnter
            unmountOnExit
            classNames={{
                enter: css.members_enter,
                enterActive: css.members_enter_active,
                exit: css.members_exit,
                exitActive: css.members_exit_active,
            }}
        >
            <div className={css.members}>
                <div className={css.members__body}>
                    <div className={css.members__head}>
                        <Button onClick={addNewUserHandler}>
                            {
                                getText(currentLanguage, {
                                    en: 'Add a new member',
                                    ua: 'Додати нового учасника'
                                })
                            }
                            <SvgIconPlusUsers />
                        </Button>
                    </div>
                    <div className={css.members__list}>
                        <h5>
                            {
                                getText(currentLanguage, {
                                    en: 'Members',
                                    ua: 'Учасники'
                                })
                            }
                        </h5>
                        {profiles.map(profile =>
                            <Box key={profile.userId}>
                                <div className={css.member_card}>
                                    <div className={css.member_card__photo}>
                                        <Link to={`/profile/${profile.userId}`}>
                                            <CircleAvatar src={profile.photos.small} />
                                        </Link>
                                    </div>
                                    <div className={css.member_card__name}>{profile.fullName}</div>
                                    {me?.id !== profile.userId &&
                                        <div className={css.member_card__delete}>
                                            <RemoveMemberFromDialog
                                                dialogId={dialogId}
                                                userId={profile.userId}
                                                onRemoved={() => onRemoveMemberFromDialogHandler(profile.userId)}
                                            />
                                        </div>
                                    }
                                </div>
                            </Box>
                        )}
                    </div>
                </div>
            </div>
        </CSSTransition>
    );
}