import React, { JSX } from 'react';
import css from './ToggleThemeButton.module.scss';
import cn from 'classnames';
import { useTheme } from 'features/ToggleTheme/hooks/hooks';

import { ReactComponent as SvgIconSun } from 'shared/assets/icons/sun.svg';
import { ReactComponent as SvgIconMoon } from 'shared/assets/icons/moon.svg';

interface ToggleThemeButtonProps {

}

export function ToggleThemeButton( {}: ToggleThemeButtonProps ): JSX.Element {
    const [theme, toggleTheme] = useTheme();

    return (
        <button onClick={() => toggleTheme()} className={css.theme_button}>
            {theme === 'dark'
                ? <SvgIconMoon />
                : <SvgIconSun />
            }
            
        </button>
    );
}