import React, { FormEvent, JSX, useState } from 'react';
import css from './UploadProfilePhotoForm.module.scss';
import cn from 'classnames';
import { useDropzone } from 'react-dropzone'

import { selectProfile, uploadPhotoAction } from 'entities/profile';
import { useAppDispatch, useAppSelector } from 'shared/store/hooks';
import { Box } from 'shared/UI/Box/Box';
import { Label } from 'shared/UI/Label/Label';
import { Button } from 'shared/UI/Button/Button';
import { setPopupMessageText, setTogglePopupMessageShow } from 'entities/popupMessage';
import { getMeAction } from 'entities/auth';
import { getText, selectLanguage } from 'features/Language';

interface Thumb extends File {
    preview: string;
}

export function UploadProfilePhotoForm(): JSX.Element {
    const [files, setFiles] = useState<Thumb[]>([]);
    const { getRootProps, getInputProps } = useDropzone({
        accept: {
            'image/*': ['.png', '.jpeg', '.jpg']
        },
        multiple: false,
        onDrop: acceptedFiles => {
            setFiles(acceptedFiles.map(file => Object.assign(file, {
                preview: URL.createObjectURL(file)
            })));
        }
    });
    const profile = useAppSelector(selectProfile);
    const dispatch = useAppDispatch();
    const currentLanguage = useAppSelector(selectLanguage);

    const onPhotoFormSubmit = (e: FormEvent) => {
        e.preventDefault();

        const onSuccess = () => {
            dispatch(setPopupMessageText('Update was success!'));
            dispatch(setTogglePopupMessageShow(true));
            dispatch(getMeAction());

            setTimeout(() => {
                dispatch(setTogglePopupMessageShow(false));
            }, 3000)
        }
        const onUnsuccess = () => {
            dispatch(setPopupMessageText('Something went wrong, please try again later.'));
            dispatch(setTogglePopupMessageShow(true));

            setTimeout(() => {
                dispatch(setTogglePopupMessageShow(false));
            }, 3000)
        }

        if (files.length) {
            dispatch(uploadPhotoAction(files[0],
                onSuccess,
                onUnsuccess
            ))
        }
    }

    return (
        <Box>
            {profile &&
                <form onSubmit={onPhotoFormSubmit}>
                    {profile.photos.small &&
                        <>
                            <h6 className='mb-2'>
                                {
                                    getText(currentLanguage, {
                                        en: 'Current Photo',
                                        ua: 'Поточна фотографія'
                                    })
                                }
                            </h6>
                            <div className={cn(css.profile_current_photo, 'mb-3')}>
                                <img src={profile.photos.small ? profile.photos.small + '?' + new Date().getTime() : undefined} alt="" />
                            </div>
                        </>
                    }
                    <div>
                        <div {...getRootProps({ className: css.dropzone })} >
                            <Label className='mb-1'>
                                {
                                    getText(currentLanguage, {
                                        en: 'Drag \'n\' drop some photo here, or click to select photo',
                                        ua: 'Перетягни сюди фото або тицьни щоб вибрати фото'
                                    })
                                }
                            </Label>
                            <input {...getInputProps()} type="file" accept=".png, .jpeg, .jpg" />
                        </div>
                        {!!files.length &&
                            <div className={cn(css.preview, 'mt-3')}>
                                {files.map((file, i) =>
                                    <div key={i} className={css.preview__item}>
                                        <img src={file.preview} alt="" />
                                        {file.name}
                                    </div>
                                )}
                            </div>
                        }
                    </div>
                    <div className="mt-3">
                        <Button disabled={!files.length} >
                            {profile.photos.small
                                ? getText(currentLanguage, {
                                    en: 'Upload new photo' ,
                                    ua: 'Завантажити нову фотографію'
                                })
                                : getText(currentLanguage, {
                                    en: 'Upload photo' ,
                                    ua: 'Завантажити фото'
                                })
                            }
                        </Button>
                    </div>
                </form>
            }
        </Box>
    );
}
