import React, { JSX } from 'react';
import css from './ProfileConfigPage.module.scss';
import cn from 'classnames';
import { Header } from 'widgets/Header';
import { ProfileConfig } from 'widgets/ProfileConfig';


export function ProfileConfigPage(): JSX.Element {
    return (
        <>
            <Header />
            <div className={cn(css.profile_page, 'container p-2 p-md-3')}>
                <ProfileConfig />
            </div >
        </>
    );
}