import React, { JSX, useState } from 'react';
import { createPortal } from 'react-dom';
import css from './DialogOptions.module.scss';
import cn from 'classnames';
import { useAppDispatch, useAppSelector } from 'shared/store/hooks';
import { deleteDialogActions } from 'entities/dialogs';
import { PopUpMessage } from 'entities/popupMessage';
import { Button } from 'shared/UI/Button/Button';
import { DropDownOptions } from 'shared/UI/DropDownOptions/DropDownOptions';
import { getText, selectLanguage } from 'features/Language';



interface DialogOptionsProps {
    dialogId: number;
}

export function DialogOptions({ dialogId }: DialogOptionsProps): JSX.Element {
    const [isConfirmOpen, setConfirmOpen] = useState<boolean>(false);
    const dispatch = useAppDispatch();
    const currentLanguage = useAppSelector(selectLanguage);

    const removeDialogHandle = () => {
        dispatch(deleteDialogActions(dialogId));
    }

    return (
        <>
            <DropDownOptions>
                <button
                    onClick={() => setConfirmOpen(true)}
                >
                    {
                        getText(currentLanguage, {
                            en: 'Remove dialog',
                            ua: 'Видалити діалог'
                        })
                    }
                </button>
            </DropDownOptions>
            {
                createPortal(
                    <PopUpMessage
                        toggleShow={isConfirmOpen}
                        onClose={(e) => {
                            e.preventDefault();
                            setConfirmOpen(false)
                        }}
                    >
                        {
                            getText(currentLanguage, {
                                en: 'Are you sure you want to delete the dialog?',
                                ua: 'Ви впевнені що хочете видалити діалог'
                            })
                        }
                        {
                            <div className="d-flex justify-content-center mt-3 gap-3">
                                <Button
                                    variant='danger'
                                    onClick={(e) => {
                                        e.preventDefault();
                                        removeDialogHandle();
                                        setConfirmOpen(false);
                                    }}
                                >
                                    {
                                        getText(currentLanguage, {
                                            en: 'Yes',
                                            ua: 'Так'
                                        })
                                    }
                                </Button>
                                <Button onClick={(e) => {
                                    e.preventDefault();
                                    setConfirmOpen(false);
                                }}>
                                    {
                                        getText(currentLanguage, {
                                            en: 'No',
                                            ua: 'Ні'
                                        })
                                    }
                                </Button>
                            </div>
                        }
                    </PopUpMessage>,

                    document.body
                )
            }
        </>
    );
}