import React, { JSX, useEffect, useRef, useState } from 'react';
import css from './Message.module.scss';
import cn from 'classnames';
import { Link } from 'react-router-dom';
import { CSSTransition } from 'react-transition-group';

import { selectMe } from 'entities/auth';
import { IProfile } from 'entities/profile/types/types';
import { IMessage } from 'entities/messages/types/types';
import { CircleAvatar } from 'entities/users';
import { deleteMessageAction } from 'entities/messages';
import { useAppDispatch, useAppSelector } from 'shared/store/hooks';

import { ReactComponent as SvgIconDelete } from 'shared/assets/icons/delete.svg';

interface MessageProps {
    dialogId: number;
    message: IMessage;
    profile?: IProfile;
    variant: 'owner' | 'companion';
}

export function Message({ dialogId, message, profile, variant }: MessageProps): JSX.Element {
    const messRef = useRef<HTMLDivElement>(null);
    const me = useAppSelector(selectMe);
    const [isShowMessageModal, setIsShowMessageModal] = useState<boolean>(false);
    const isOwner = me?.id === message.userId;

    const onClickHandler = (e: React.MouseEvent) => {
        e.preventDefault();
        setIsShowMessageModal(true);
    }

    const handleOusideClick = (e: MouseEvent) => {
        if (messRef && !messRef.current?.contains(e.target as Node)) {
            setIsShowMessageModal(false);
        }
    }

    useEffect(() => {
        document.addEventListener('click', handleOusideClick, false);
        document.addEventListener('contextmenu', handleOusideClick, false);
        return () => {
            document.removeEventListener('click', handleOusideClick, false)
            document.removeEventListener('contextmenu', handleOusideClick, false)
        };
    }, []);

    return (
        <>
            <div
                onClick={onClickHandler}
                ref={messRef}
                className={cn(css.message, css[variant], { [css.active]: isShowMessageModal && isOwner })}
            >
                <div className={css.message__head}>
                    <div className={css.message__photo}>
                        {profile
                            ? <Link to={'/profile/' + profile.userId}>
                                <CircleAvatar src={profile.photos.small} />
                            </Link>
                            : <CircleAvatar />
                        }
                    </div>
                    <div className={css.message__date}>
                        {new Date(message.createdAt).toLocaleString()}
                    </div>
                </div>
                <div className={css.message__content}>
                    {message.text}
                </div>
            </div>
            {me && isOwner &&
                <DeleteMessageModal
                    isShow={isShowMessageModal}
                    dialogId={dialogId}
                    messageId={message.id}
                    onClick={() => setIsShowMessageModal(false)}
                />
            }
        </>
    );
}

interface DeleteMessageModalProps {
    isShow: boolean;
    dialogId: number;
    messageId: number;
    onClick: () => void
}
function DeleteMessageModal({ isShow, dialogId, messageId, onClick }: DeleteMessageModalProps): JSX.Element {
    const dispatch = useAppDispatch();

    const deletMessageHandler = () => {
        dispatch(deleteMessageAction(dialogId, messageId));
        onClick();
    }
    const nodeRef = useRef(null)
    return (
        <CSSTransition
            in={isShow}
            timeout={300}
            nodeRef={nodeRef}
            mountOnEnter
            unmountOnExit
            classNames={{
                enter: css.modal_enter,
                enterActive: css.modal_enter_active,
                exit: css.modal_exit,
                exitActive: css.modal_exit_active,
            }}
        >
            <div ref={nodeRef} className={css.modal}>
                <button onClick={deletMessageHandler}>
                    <SvgIconDelete />
                </button>
            </div>
        </CSSTransition>
    );
}
