import React, { useEffect } from 'react';
import './App.scss';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from 'shared/store/hooks';
import { checkAuthAction, selectIsAuth, selectIsAuthCompleted } from 'entities/auth';
import { privateRoutes, publicRoutes } from './routes/routes';
import { SpinerOverlay } from 'shared/UI/Spiner/Spiner';
import { useTheme } from 'features/ToggleTheme';
import { checkInstalledLanguageAction } from 'features/Language';



function App() {
    const isAuth = useAppSelector(selectIsAuth);
    const isAuthCompleted = useAppSelector(selectIsAuthCompleted);
    const dispatch = useAppDispatch();
    useTheme();
    
  

    useEffect(() => {
        dispatch(checkAuthAction());
        dispatch(checkInstalledLanguageAction());
    }, [isAuth]);

    if (!isAuthCompleted) {
        return <SpinerOverlay size={1.5} />
    } else {
        return (
            <div className="App">
                <BrowserRouter>
                    {isAuth
                        ? <Routes>
                            {privateRoutes.map(route =>
                                <Route key={route.path} {...route} />
                            )}
                        </Routes>
                        : <Routes>
                            {publicRoutes.map(route =>
                                <Route key={route.path} {...route} />
                            )}
                        </Routes>
                    }
                </BrowserRouter>
            </div>
        );
    }
}

export default App;