import React, { JSX, ReactElement, ReactNode, HtmlHTMLAttributes } from 'react';
import css from './FlexCenterBox.module.scss';
import cn from 'classnames';

interface FlexCenterBoxProps extends HtmlHTMLAttributes<Element> {
    classNames?: string[];
    children?: ReactElement | ReactNode;
}

export function FlexCenterBox( { children, classNames = [], ...props }: FlexCenterBoxProps ): JSX.Element {
    return (
        <div className={cn(css.box, ...classNames)} {...props}>
            {children}
        </div>
    );
}