import React, { JSX } from 'react';
import css from './Header.module.scss';
import cn from 'classnames';
import { Link } from 'react-router-dom';
import { ReactComponent as SvgIconSearch } from 'shared/assets/icons/search.svg';
import { ReactComponent as SvgIconMessages } from 'shared/assets/icons/messages.svg';
import { CircleAvatar } from 'entities/users/index';
import { selectMe } from 'entities/auth/index';
import { useAppSelector } from 'shared/store/hooks';
import { DropDownOptions } from 'shared/UI/DropDownOptions/DropDownOptions';
import { useAppDispatch } from 'shared/store/hooks';
import { logOutAction } from 'entities/auth';
import { ToggleThemeButton } from 'features/ToggleTheme';
import { LanguageSwitcher } from 'features/Language';

export function Header(): JSX.Element {
    const me = useAppSelector(selectMe);
    const dispatch = useAppDispatch();

    const handleLogOut = () => {
        dispatch(logOutAction())
    }

    return (
        <div className={css.header}>
            <div className={css.left}>
                <div className={css.avatar}>
                    <Link to='/profile'>
                        <CircleAvatar src={me?.photos.small && me?.photos.small + '?' + new Date().getTime()} />
                        {me && <div className="mt-1 text-size-2 text-center">
                            {me.login}
                        </div>}
                    </Link>
                </div>
            </div>
            <div className={css.right}>
                <div className={css.right_item}>
                    <LanguageSwitcher />
                </div>
                <div className={css.right_item}>
                    <ToggleThemeButton />
                </div>
                <div className={css.right_item}>
                    <Link to='/users'>
                        <SvgIconSearch className={css.header_icon} />
                    </Link>
                </div>
                <div className={css.right_item}>
                    <Link to='/dialogs'>
                        <SvgIconMessages className={css.header_icon} />
                    </Link>
                </div>
                <div className={css.right_item}>
                    <DropDownOptions>
                        <Link to="/profile/configuration">Update Profile</Link>
                        <button onClick={handleLogOut}>Log Out</button>
                    </DropDownOptions>
                </div>
            </div>
        </div>
    );
}
