import { RootState } from "shared/store/types";
import { IMe, RegistrStatus } from "../types/types";


export const selectIsAuth = (state: RootState): boolean => state?.authReducer?.isAuth || false;
export const selectIsAuthCompleted  = (state: RootState): boolean => state?.authReducer?.isAuthCompleted || false;
export const selectMe = (state: RootState): IMe | null => state?.authReducer?.me || null;
export const selectIsFetching = (state: RootState): boolean => state?.authReducer?.isFetching || false;
export const selectIsLoginError = (state: RootState): boolean => state?.authReducer?.isLoginError || false; 
export const selectIsRegistrError = (state: RootState): boolean => state?.authReducer?.isRegistrError || false; 
export const selectRegistrErrorText = (state: RootState): string => state?.authReducer?.registrErrorText || ''; 
export const selectRegistrStatus = (state: RootState): RegistrStatus => state?.authReducer?.registrStatus || null; 
