import { AppDispatch } from "shared/store/types";
import { 
    setIsAuth, 
    setIsAuthCompleted, 
    setIsFetching, 
    setIsLoginError, 
    setIsRegistrError, 
    setMe, 
    setRegistrErrorText,
    setRegistrStatus
} from "./slice";
import { AuthApi } from "../api/AuthApi";
import { RootApi } from "shared/api/RootApi";

interface IRegistrErrorMessages {
    userExistsMessage: string;
    wrongDataMessage: string;
}

export const userLoginAction = (email: string, password: string) => async (dispath: AppDispatch) => {
    dispath(setIsFetching(true));
    let authResponse = await AuthApi.login(email, password);
    if (authResponse && authResponse.data.resultCode === 0) {
        localStorage.setItem('jwt', authResponse.data.data.jwt);
        dispath(setIsAuth(true));
    } else {
        dispath(setIsLoginError(true));
    }
    dispath(setIsFetching(false));
}

export const registrAction = (
    email: string,
    login: string,
    password: string,
    errorMessages: IRegistrErrorMessages,
    callbackAfterSuccessFetch: () => void
) => async (dispath: AppDispatch) => {
    debugger;
    dispath(setIsFetching(true));
    let response = await AuthApi.register(email, login, password);
    if (response && response.data.resultCode === 0) {
        dispath(setRegistrStatus(response.data.resultCode));
        dispath(setIsRegistrError(false));
        callbackAfterSuccessFetch();
    } else if (response && response.data.resultCode === 1) {
        dispath(setRegistrStatus(response.data.resultCode));
        dispath(setIsRegistrError(true));
        dispath(setRegistrErrorText(errorMessages.userExistsMessage))
    } else if (response && response.data.resultCode === 2) {
        dispath(setRegistrStatus(response.data.resultCode));
        dispath(setIsRegistrError(true));
        dispath(setRegistrErrorText(errorMessages.wrongDataMessage))
    }
    dispath(setIsFetching(false));
}

export const checkAuthAction = () => async (dispath: AppDispatch) => {
    const jwt = localStorage.getItem('jwt');
    if (jwt) {
        RootApi.setPrivate(jwt);
        const response = await AuthApi.me();
        if (response && response.data.resultCode === 0) {
            dispath(setMe(response.data.data));
        }
        dispath(setIsAuth(true));
        dispath(setIsAuthCompleted(true));
    } else {
        RootApi.setPublick();
        dispath(setIsAuth(false));
        dispath(setIsAuthCompleted(true));
    }
}

export const logOutAction = () => async (dispath: AppDispatch) => {
    RootApi.setPublick();
    localStorage.removeItem('jwt');
    dispath(setIsAuth(false));
}

export const getMeAction = () => async (dispath: AppDispatch) => {
    const response = await AuthApi.me();
    if (response && response.data.resultCode === 0) {
        dispath(setMe(response.data.data));
    }
}