import { AppDispatch, RootState } from "shared/store/types";
import { setIsFetching, setMessages, setPage, setTotalCount, setWs } from "./slice";
import { MessagesApi } from "../api/MessagesApi";
import { store } from "app/store/store";

let WS: WebSocket | null = null;

export const creatWSAction = (callback: (ws: WebSocket) => void) => async (dispatch: AppDispatch) => {
    const ws = MessagesApi.createWs();

    WS = ws;
    callback(ws);

    ws.onopen = () => {
        console.log('open ws');
    }

    ws.onmessage = async (event: MessageEvent<string>) => {
        const state = store.getState();
        const { messages } = state.messagesReducer;
        const data = await JSON.parse(event.data);
        dispatch(setMessages([...messages, data]));
    }

    ws.onclose = (event) => {
        console.log('WebSocket connection closed.', event);
    }
}

export const getMessagesAction = (dialogId: number) => async (dispatch: AppDispatch) => {
    const messagesReducer = store.getState().messagesReducer;
    const { count } = messagesReducer;
    dispatch(setIsFetching(true));
    const response = await MessagesApi.getMessages(dialogId, count);
    if (response && response.data.resultCode === 0) {
        dispatch(setMessages(response.data.data.items));
        dispatch(setTotalCount(response.data.data.totalCount));
        dispatch(setPage(Math.ceil(response.data.data.totalCount / count)));
    }
    dispatch(setIsFetching(false));
}

export const loadPrevMessagesAction = (dialogId: number) => async (dispatch: AppDispatch) => {
    const state = store.getState();
    const { messages, count, page } = state.messagesReducer;
    if(page === 1) return;
    const response = await MessagesApi.getMessages(dialogId, count, page -1);
    if(response && response.data.resultCode === 0) {
        dispatch(setMessages([...response.data.data.items, ...messages]));
        dispatch(setPage(page - 1));
    }
}

export const createMessageAction = (dialogId: number, text: string) => async (dispatch: AppDispatch) => {
    const state = store.getState();
    if(WS) {
        WS.send(
            JSON.stringify({
                dialogId,
                text
            })
        )
    }
}

export const deleteMessageAction = (dialogId: number, messageId: number) => async (dispatch: AppDispatch) => {
    dispatch(setIsFetching(true));
    const response = await MessagesApi.deleteMessage(dialogId, messageId);
    if (response && response.data.resultCode === 0) {
        const state = store.getState();
        const { messages } = state.messagesReducer;
        const fileteredMessages = messages.filter(mess => mess.id !== messageId);
        dispatch(setMessages(fileteredMessages));
    }
    dispatch(setIsFetching(false));
}
